var	 _this;
export default{
	data() {
		return{
			id:'',
			dataArr:[],
			baseUrl: "http://39.105.48.243:8080/crlink/"
		}
	},
	onLoad(e) {
		_this = this;
		this.id = e.id
		this.getMoreCollege()
	},
	
	methods:{
			
		getMoreCollege(){
			var loginkey = uni.getStorageSync('loginKey');
			this.$api.post('index!ajaxGetMoreCollege.action', {
				id: _this.id,
				loginKey: loginkey,
			}).then(res => {
				if (res.res.status == 0) {
					_this.dataArr = res.inf.arr;
				}else{
					uni.showToast({
						title:res.res.error
					})
				}
			})
		},
		itemClcik(item){
			uni.navigateTo({
				url:'./college?collegeId='+item.collegeId
			})
		}
	}
}

